import React from 'react';
import { Form, Input, Select, Button } from 'antd';
import locationSearch from 'utils/locationSearch';

const FormItem = Form.Item;
const Option = Select.Option;

class Filter extends React.Component {

  componentDidMount() {
    //
    this.handleReset(true);
  }

  //
  handleSearch = () => {
    const values = this.props.form.getFieldsValue();
    const dealedValues = { pageNo: 1 }; //
    //
    for(let attr in values) {
      dealedValues[attr] = values[attr];
    }

    locationSearch.set(dealedValues);
    this.props.onChange();
  }

  //()
  handleReset = (isInit) => {
    if (isInit !== true) {
      locationSearch.cover({ pageNo: 1 });
    }
    this.mapSearchToForm();
    this.props.onChange();
  }

  //form
  mapSearchToForm = () => {
    const filter = locationSearch.get();
    
    this.props.form.setFieldsValue({
      name: filter.name,
      country: filter.country
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <Form layout="inline" className="table-filter-form">
        <FormItem label="">
          {getFieldDecorator('name')(
            <Input allowClear placeholder="" style={{ width: 200 }} />
          )}
        </FormItem>
        <FormItem label="/">
          {getFieldDecorator('country')(
            <Select allowClear placeholder="" style={{ width: 200 }}>
              {this.props.countryList.map(item => <Option key={item.code} value={String(item.code)}>{item.name}</Option>)}
            </Select>
          )}
        </FormItem>

        {/*  */}
        <FormItem colon={false}>
          <Button.Group>
            <Button icon="search" onClick={this.handleSearch}></Button>
            <Button icon="redo" onClick={this.handleReset}></Button>
          </Button.Group>
        </FormItem>
      </Form>
    );
  }
}

export default Form.create({})(Filter);
